import mList from '@/components/m-list.vue';
import mInput from '@/components/m-input.vue';
import service from '../../static/service/service.js';
import uniPopup from "@/components/uni-popup/uni-popup.vue";
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue";

export default {
	components: {
		mList,
		mInput,
		uniPopup,
		uniNumberBox
	},
	data() {
		return {
			// 请求对象
			params: {
				'tableId': "010401",
				'pageNumber': 1,
				'pageSize': 10,
				'queryKey': ""
			},
			isLoadMore: true,
			activeRow: {},
			dataList: [],
			// 用户输入的内容
			inputContent: '',
			jgsl: 0,
			radioList:[
				{
					value:'0',
					name:'全部提交',
					checked: 'true'
				},{
					value:'1',
					name:'部分提交'
				},
			],
			current: 0,
			// 提交页面的属性
			isHUDShow:false,
			// 提交按钮高度值
			partViewHeight:37
		}
	},

	// 进入页面后刷新数据
	onShow() {
		this.params.pageNumber = 1
		this.params.queryKey = ""
		this.initData(0)
	},

	// 下拉刷新
	onPullDownRefresh() {
		this.params.pageNumber = 1
		this.initData(0)
		this.isLoadMore = true
		uni.showToast({
			'icon': 'none',
			'title': '刷新成功'
		})
		uni.stopPullDownRefresh()
	},

	// 加载更多数据
	onReachBottom() {
		if (this.isLoadMore) {
			this.params.pageNumber++
			this.initData(0)
		}
	},
	methods: {
		async initData(type) {
			// type==0 列表刷新 params不包含queryKey
			// type==1 搜索刷新 params包含queryKey
			if (type == 0) {
				let res = await service.taskList(this.params);
				if (res.errno == 0) {
					if (this.params.pageNumber == 1) {
						this.dataList = res.data.data;
					} else {
						this.dataList = this.dataList.concat(res.data.data);
					}
					if (res.data.data.length < 10) {
						this.isLoadMore = false;
					}
				}
			} else {
				let res = await service.taskListSearch(this.params);
				if (res.errno == 0) {
					if (this.params.pageNumber == 1) {
						this.dataList = res.data.data;
					} else {
						this.dataList = this.dataList.concat(res.data.data);
					}
					if (res.data.data.length < 10) {
						this.isLoadMore = false;
					}
				}
			}
		},
		// 用户搜索后刷新数据
		listingValueChange() {
			this.params.pageNumber = 1
			this.params.queryKey = this.inputContent
			this.initData(1)
		},
		// 点击提交任务,弹出选择提交类型页面
		handleSubmitTask(row) {
			this.activeRow = row
			this.jgsl = this.activeRow.JGSL
			this.isHUDShow = true;
		},
		// 点击radio后方法
		radioChange: function(evt) {
			this.current = evt.detail.value 
			if (evt.detail.value == 0) { 
				this.partViewHeight = 37;
			} else {
				this.partViewHeight = 70;
			}
		},
		//点击提交按钮调用方法
		async onSubmit(){
			let params = {
				gyid: this.activeRow.ID,
				jgjs: this.jgsl
			}
			let res = await service.submitTask(params);
			if (res.errno == 0) {
				uni.showToast({
					title: res.errmsg
				});
				this.isHUDShow = false
				this.initData();
			} else {
				uni.showToast({
					title: res.errmsg
				});
			}
		},
		closePopup(val) {
			this.isHUDShow = val.show
		},
		numberChange(val) {
			this.jgsl = val
		}
	}
}
